@import "../../scss/variables";

.card-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    .cards{
        overflow: hidden;
        background-color:black;
        margin: 1rem;
        padding: 1rem;
        border-radius: 10px;
        box-shadow: 3px 3px 5px rgba(0,0,0,0.7);
        position: relative;
        &:hover .card-over{
            transform: translateY(0%);
            transition: transform 0.9s ease-in-out ;
        }
    }
    .title{
        color:$backsgroundColor;
        padding: 1rem;
        font-size: 1.5rem;
    }
    img{
        width:500px;
        height: 350px;
        object-fit: cover;
        filter:grayscale(0%);
        &:hover{filter:grayscale(80%);}
}


}
.card-over{
  color:white;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(0,0,0,0.5);
  font-size: 1.5rem;
  padding: 1rem;
  transform: translateY(100%);
  z-index: 1;

}